.App {
  text-align: center;
}

.App-header {
  width: 100vw;
  height: 100vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(255, 255, 255);
} 

.hikingMap {
  width: 100vw;
  height: 100vh;
}

.hikingMap .mapContainer {
  width: 100%;
  height: 100%;
}

.hikingMap .markerInfoImg {
  width: 100px;
  position: absolute;
  bottom: 7%;
  right: 2.5%; 
  z-index: 444;
}

.hikingMap .info {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 400px;
  height: 100vh;
  z-index: 999;
  border-radius: 0 15px 15px 0;
  background-color: #000;
}

.hikingMap .info.closed {
  transform: translateX(-100%);
  transition: all 0.5s;
}

.hikingMap .info.open {
  transform: translateX(0);
  transition: all 0.5s;
}

.hikingMap .info .infoToggle {
  width: 25px;
  height: 50px;
  position: absolute;
  top: 50px;
  left: 400px;
  background-color: rgba(106, 106, 106, 0.5);
  border-radius: 0 5px 5px 0;
  z-index: 999;
}

.hikingMap .info .infoToggle:hover {
  background-color: rgba(39, 39, 39, 0.5);
}

.hikingMap .info .search {
  width: 100%;
  height: 10%;
}

.hikingMap .info .search > input {
  width: 90%;
  height: 40px;
  border-radius: 15px;
  margin-top: 15px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

.hikingMap .info .search > ul {
  position: absolute;
  left: 50%;
  width: 90%;
  height: 100%;
  list-style: none;
  padding: 0;
  margin: 10px auto;
  transform: translateX(-50%);
}

.hikingMap .info .search > ul > li {
  margin-bottom: 10px;
}

.hikingMap .info .search > ul > li > button {
  width: 90%;
  height: 50px;
  border-radius: 30px;
  background-color: #e6e4e4;
  border: 3px solid black;
  font-size: 13px;
  color: rgb(68, 68, 68);
}

.hikingMap .info .search > ul > li > button:hover {
  background-color: rgb(207, 207, 207);
}

.hikingMap .info .search > ul > li > button > strong {
  font-size: 15px;
  color: #000;
}

.hikingMap .info .tourList {
  width: 100%;
  height: 90%;
}

.hikingMap .info .tourList > h3 {
  width: 100%;
  height: 35px;
  margin: 20px 0;
  font-size: 30px;
}

.hikingMap .info .tourList > ul {
  padding: 0;
  height: calc(100% - 35px);
  overflow: auto;
}

.hikingMap .info .tourList > ul > li {
  list-style: none;
  font-size: 13px;
  color: rgb(188, 188, 188);
  margin-top: 20px;
}

.hikingMap .info .tourList > ul > li:first-child{
  margin-top: 0px;
}

.hikingMap .info .tourList > ul > li > strong {
  font-size: 20px;
  color: #fff;
}



@media (max-width: 480px){
  .hikingMap .info {
    width: 100%;
    height: 90%;
    top: auto;
    bottom: 0px;
    border-radius: 15px 15px 0 0;
  }

  .hikingMap .info.closed {
    transform: translateY(100%);
    transition: all 0.5s;
  }
  
  .hikingMap .info.open {
    transform: translateY(0);
    transition: all 0.5s;
  }

  .hikingMap .info .infoToggle {
    top: -25px;
    left: 50%;
    width: 50px;
    height: 25px;
    transform: translateX(-50%);
    border-radius: 5px 5px 0 0;
  }

  .hikingMap .info .infoToggle img {
    transform: rotate(-90deg) translateX(12.5px);
  }
}